.about-section {
  display: -ms-flexbox;
  display: flex;
  position: relative;

  height: 700px;
}

.about-section__photo {
  height: 500px;
  min-width: 750px;
  max-width: 800px;
  background-color: black;
  border-top-right-radius: 15px;
  border-bottom-right-radius: 15px;
  -webkit-box-shadow: 0px 4px 16px #303854;
          box-shadow: 0px 4px 16px #303854;

  position: absolute;
  -webkit-transition: 1s;
  -o-transition: 1s;
  transition: 1s;

  left: -1000px;
  opacity: 0;
}

.about-section__photo-credits {
  position: absolute;
  -webkit-transition: 1s;
  -o-transition: 1s;
  transition: 1s;
  left: -1000px;
  opacity: 0;
  color: white;
  font-size: 16px;
  z-index: 100;
  margin-left: 15px;
  top: 470px;
}

.photoShow {
  -webkit-transition: 1s;
  -o-transition: 1s;
  transition: 1s;
  left: 0px;
  opacity: 1;
}

.about-section__info {
  height: 450px;
  width: 57%;
  min-width: 800px;
  background-color: #525c7b;
  border-top-left-radius: 15px;
  border-bottom-left-radius: 15px;
  -webkit-box-shadow: 0px 4px 16px #303854;
          box-shadow: 0px 4px 16px #303854;

  top: 80px;
  right: 1500px;

  position: absolute;

  color: white;

  display: -ms-flexbox;

  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;

  -webkit-transition: 1s;

  -o-transition: 1s;

  transition: 1s;

  opacity: 0;
}

.infoShow {
  -webkit-transition: 1s;
  -o-transition: 1s;
  transition: 1s;
  right: 0px;

  opacity: 1;
}

.about-section__info__content {
  width: 75%;
}

.about-section__info__content__title {
  font-size: 36px;
  font-weight: bolder;
}

.about-section__info__content__subtitle {
  font-size: 28px;
  margin-bottom: 20px;
}

.about-section__info__content__body {
  font-size: 18px;
}



