.navbar {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  position: fixed;
  width: 100%;
  z-index: 100;
  height: 75px;
  padding-right: 50px;
  color: white;
  -webkit-transition: 0.2s;
  -o-transition: 0.2s;
  transition: 0.2s;
}

.navbar-active {
  color: white;
  background-color: #303854;
  -webkit-box-shadow: 1px 1px 5px black;
          box-shadow: 1px 1px 5px black;
  -webkit-transition: 0.2s;
  -o-transition: 0.2s;
  transition: 0.2s;
}

.navbar-logo-container {
  -ms-flex: 1 1;
      flex: 1 1;
  padding-left: 50px;

  font-size: 20px;
}

.navbar-logo {
  width: 50px;
  height: 50px;
  margin-right: 20px;
}

.navbar-logo:hover {
  cursor: pointer;
}

.navbar-items-container {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: end;
      justify-content: flex-end;
  -ms-flex: 8 1;
      flex: 8 1;
}

.navbar-item {
  margin-right: 30px;
  font-size: 18px;
  -webkit-transition: 0.3s;
  -o-transition: 0.3s;
  transition: 0.3s;
  color: white;
}

.navbar-item:hover {
  cursor: pointer;
  color: lightgrey;
  text-decoration: none;
}



